<div class="mdc-typography--headline2 uppercase">Libby, Montana (USA)</div>

<div class="left">
  <div class="mdc-typography--headline4 uppercase">Storia del passato</div>
  <div class="mdc-typography--body1">
    La cittadina di Libby in Montana è il sito di uno dei peggiori disastri ambientali causati dall'uomo. La polvere di amianto tossico proveniente dalle
    miniere di vermiculite ha ucciso centinaia di residenti, ammalandone altre migliaia.
    <br /><br />
    Nel 1963, la W.R. Grace & Company, iniziò a scavare un deposito di vermiculite su una cima boschiva vicino a Libby. Il minerale, relativamente innocuo, è stato
    utilizzato come isolamento delle soffitte fino agli anni 1980. Ma nello stesso deposito è stato trovato un tipo naturale di amianto mortale.
    <br /><br />
    Per decenni, la miniera ha prodotto tonnellate di polvere al giorno, che non solo fluttuava ovunque negli stabilimenti, ma inondava l'aria respirabile di gran
    parte della città. L'azienda sapeva che la vermiculite era contaminata dall'amianto e che causava complicazioni per la salute. La società non avvertì nessuno
    dell'esposizione all'amianto e l'attività mineraria continuò fino al 1990.
  </div>
</div>

<div class="right">
  <div class="mdc-typography--headline4 uppercase primary">Conseguenze sul presente</div>
  <div class="mdc-typography--body1">
    Quando l'azienda venne chiusa, gli operai del governo chiamati per il risanamento hanno trovato l'amianto quasi dappertutto: nei polmoni degli operai e
    delle loro famiglie, sul campo da baseball e sulle piste da corsa del liceo a cui l'azienda aveva donato il materiale per la copertura.
    <br /><br />
    Gli effetti dolorosi continuano a svolgersi lentamente: infatti ci vogliono decenni perchè i sintomi delle malattie da amianto si manifestino, e i bambini che
    hanno giocato a baseball o corso sulle piste a contatto con l'amianto stanno iniziando ad ammalarsi solo ora.
  </div>
</div>

